*{
    box-sizing: border-box;
}

html,body{
    position: fixed;
}

#root{
    background-color: crimson;
    overflow-y: hidden;

}
.wrapper{
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background: -o-linear-gradient(135deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	-webkit-animation: gradient 15s ease infinite;
	        animation: gradient 15s ease infinite;
    overflow-x: scroll;
    overflow-y: hidden;
}
.page{
    
    min-width: 100vw;
    max-width: 100vw;
    min-height: 90vh;
   

  
} 

.page-one{
    
    display: -webkit-box;
    
    display: -ms-flexbox;
    
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    padding: 1vh 0;
    background: transparent;
    overflow-y: scroll;
}
.page-one__form{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    min-height: 50px;
}

.page-one__form textarea{
    -webkit-box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.1);
            box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    font-size: 16px;
    resize: none;
}
.page-one__form button {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background: #3ac26e;
    -webkit-box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.1);
            box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    border: none;
    width: 25%;
    color: #fff;
    font-family: Lato, Arial, sans-serif;
    text-transform: uppercase;
    text-decoration: none;
    -webkit-transition: background-color .3s, -webkit-transform .3s, -webkit-box-shadow .3s;
    transition: background-color .3s, -webkit-transform .3s, -webkit-box-shadow .3s;
    -o-transition: background-color .3s, transform .3s, box-shadow .3s;
    transition: background-color .3s, transform .3s, box-shadow .3s;
    transition: background-color .3s, transform .3s, box-shadow .3s, -webkit-transform .3s, -webkit-box-shadow .3s;
    will-change: transform;
}

.page-one__form button:hover {
    background: darken(#3ac26e, 10%);
    -webkit-box-shadow: 0 4px 17px rgba(0, 0, 0, 0.2);
            box-shadow: 0 4px 17px rgba(0, 0, 0, 0.2);
    -webkit-transform: translate3d(0, -2px, 0);
            transform: translate3d(0, -2px, 0);
}

.page-one__form button:focus {
    outline: none;
}

.page-one__form button:active {
    -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
            box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
    -webkit-transform: translate3d(0, 1px, 0);
            transform: translate3d(0, 1px, 0);
}

.pulse {
    position: relative;
}

.pulse:before,
.pulse:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, .4);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    opacity: 0;
    margin: auto;
}

.pulse:before {
    -webkit-animation: pulse 1.5s infinite linear;
            animation: pulse 1.5s infinite linear;
}

.pulse:after {
    -webkit-animation: pulse 2s .4s infinite linear;
            animation: pulse 2s .4s infinite linear;
}

.pulse:hover:before,
.pulse:hover:after {
    display: none;
}

.page-one__container{
    width: 250px;
    height: 100%;
}

.text{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    font-size: 20px; 
    word-wrap:break-word ;
    word-break: break-all;
    color: white;
    text-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.1);
    font-weight: 500;
}

.text button{
    border: none;
    display: inline;
    outline: none;
    background-color: transparent;
    color: rgb(224, 18, 18);
    
    -webkit-transition: all 0.3s linear;
    
    -o-transition: all 0.3s linear;
    
    transition: all 0.3s linear;
}
.text button:hover{
    font-size:x-large;
    font-weight: 900;
}

.text-new{
    white-space: wrap;
    position: relative;
    overflow: hidden;
    border-right: solid 3px rgba(0, 255, 0, 0);
    -webkit-animation: animated-text 3s steps(44,end) 0.1s 1 normal both, animated-cursor 600ms steps(44,end) 10;
            animation: animated-text 3s steps(44,end) 0.1s 1 normal both, animated-cursor 600ms steps(44,end) 10
}

.page-two{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: transparent;
   overflow-y: hidden;
    max-height: 100vh;
    
}

.time{
    font-size: 25px;
    font-weight: 900;
    color: white;
    text-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.288);
}

@-webkit-keyframes animated-text{
    from{left: -100px;width: 0; opacity: 0;}
    to{left: 0;width: 250px; opacity: 1;}
}



@keyframes animated-text{
    from{left: -100px;width: 0; opacity: 0;}
    to{left: 0;width: 250px; opacity: 1;}
}



@-webkit-keyframes animated-cursor{
    from{border-right-color: rgb(0, 0, 0);}
    to{border-right-color: transparent;}
}



@keyframes animated-cursor{
    from{border-right-color: rgb(0, 0, 0);}
    to{border-right-color: transparent;}
}

@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@-webkit-keyframes pulse {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }
    33% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(3);
                transform: scale(3);
        opacity: 0;
    }
}

@keyframes pulse {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }
    33% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(3);
                transform: scale(3);
        opacity: 0;
    }
}

